import { supabase } from '../services/supabaseService';
import { Salao } from '../contexts/SaloesContext';

/**
 * Script para migrar dados existentes do localStorage para o Supabase
 * Execute este script uma vez aps configurar o Supabase
 */

export const migrateLocalStorageToSupabase = async (): Promise<void> => {
  try {
    console.log(' Iniciando migrao dos dados para o Supabase...');
    
    // Verificar se o Supabase est configurado
    const { data: testData, error: testError } = await supabase
      .from('saloes')
      .select('count')
      .limit(1);
    
    if (testError) {
      throw new Error(`Erro de conexo com Supabase: ${testError.message}`);
    }
    
    // Buscar dados do localStorage
    const localData = localStorage.getItem('saloes');
    if (!localData) {
      console.log(' Nenhum dado encontrado no localStorage.');
      return;
    }
    
    const saloes: Salao[] = JSON.parse(localData);
    console.log(` Encontrados ${saloes.length} sales no localStorage`);
    
    if (saloes.length === 0) {
      console.log(' Nenhum salo para migrar.');
      return;
    }
    
    // Verificar se j existem dados no Supabase
    const { data: existingData, error: existingError } = await supabase
      .from('saloes')
      .select('id')
      .limit(1);
    
    if (existingError) {
      throw new Error(`Erro ao verificar dados existentes: ${existingError.message}`);
    }
    
    if (existingData && existingData.length > 0) {
      const shouldProceed = window.confirm(
        'J existem dados no Supabase. Deseja continuar com a migrao? Isso pode criar duplicatas.'
      );
      if (!shouldProceed) {
        console.log(' Migrao cancelada pelo usurio.');
        return;
      }
    }
    
    // Preparar dados para insero no Supabase
    const saloesForSupabase = saloes.map(salao => ({
      id: salao.id,
      nome: salao.nome,
      endereco: salao.endereco,
      telefone: salao.telefone,
      email: salao.email || '',
      logo: salao.logo || '',
      site_url: salao.siteUrl || '',
      horario_funcionamento: salao.horarioFuncionamento || '',
      servicos: salao.servicos || [],
      descricao: salao.descricao || '',
      ativo: salao.ativo ?? true,
      created_at: new Date().toISOString(),
      updated_at: new Date().toISOString()
    }));
    
    // Inserir dados no Supabase
    console.log(' Inserindo dados no Supabase...');
    const { data, error } = await supabase
      .from('saloes')
      .insert(saloesForSupabase)
      .select();
    
    if (error) {
      throw new Error(`Erro ao inserir dados: ${error.message}`);
    }
    
    console.log(` Migrao concluda com sucesso! ${data?.length || 0} sales migrados.`);
    
    // Perguntar se deve fazer backup do localStorage
    const shouldBackup = window.confirm(
      'Migrao concluda! Deseja fazer backup dos dados do localStorage antes de limp-los?'
    );
    
    if (shouldBackup) {
      // Criar backup
      const backupData = {
        timestamp: new Date().toISOString(),
        saloes: saloes
      };
      
      const backupBlob = new Blob([JSON.stringify(backupData, null, 2)], {
        type: 'application/json'
      });
      
      const url = URL.createObjectURL(backupBlob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `saloes-backup-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      
      console.log(' Backup do localStorage criado e baixado.');
    }
    
    // Perguntar se deve limpar o localStorage
    const shouldClear = window.confirm(
      'Deseja limpar os dados do localStorage agora que esto no Supabase?'
    );
    
    if (shouldClear) {
      localStorage.removeItem('saloes');
      console.log(' Dados do localStorage removidos.');
    }
    
    alert('Migrao concluda com sucesso! Recarregue a pgina para ver os dados do Supabase.');
    
  } catch (error) {
    console.error(' Erro durante a migrao:', error);
    alert(`Erro durante a migrao: ${error instanceof Error ? error.message : 'Erro desconhecido'}`);
    throw error;
  }
};

/**
 * Funo para verificar o status da migrao
 */
export const checkMigrationStatus = async (): Promise<{
  hasLocalData: boolean;
  hasSupabaseData: boolean;
  localCount: number;
  supabaseCount: number;
}> => {
  try {
    // Verificar dados locais
    const localData = localStorage.getItem('saloes');
    const localSaloes = localData ? JSON.parse(localData) : [];
    
    // Verificar dados no Supabase
    const { data: supabaseData, error } = await supabase
      .from('saloes')
      .select('id');
    
    if (error) {
      console.error('Erro ao verificar dados do Supabase:', error);
      return {
        hasLocalData: localSaloes.length > 0,
        hasSupabaseData: false,
        localCount: localSaloes.length,
        supabaseCount: 0
      };
    }
    
    return {
      hasLocalData: localSaloes.length > 0,
      hasSupabaseData: (supabaseData?.length || 0) > 0,
      localCount: localSaloes.length,
      supabaseCount: supabaseData?.length || 0
    };
    
  } catch (error) {
    console.error('Erro ao verificar status da migrao:', error);
    return {
      hasLocalData: false,
      hasSupabaseData: false,
      localCount: 0,
      supabaseCount: 0
    };
  }
};

/**
 * Funo para executar a migrao automaticamente se necessrio
 */
export const autoMigrateIfNeeded = async (): Promise<boolean> => {
  try {
    const status = await checkMigrationStatus();
    
    // Se h dados locais mas no h dados no Supabase, sugerir migrao
    if (status.hasLocalData && !status.hasSupabaseData) {
      const shouldMigrate = window.confirm(
        `Encontrados ${status.localCount} sales no armazenamento local. ` +
        'Deseja migrar esses dados para o Supabase agora?'
      );
      
      if (shouldMigrate) {
        await migrateLocalStorageToSupabase();
        return true;
      }
    }
    
    return false;
  } catch (error) {
    console.error('Erro na migrao automtica:', error);
    return false;
  }
};